<script>
import { mapState } from 'vuex';
import axios from 'axios';

export default {
  name: 'TopRewardsPage',
  data() {
    return {
      activeTab: 'twitter',
      activeLeaderboardTab: 'twitter',
      isLoading: true,
      isLoadingTopUsers: true,
      weeklyRewards: [],
      topTwitterUsers: [],
      topTelegramUsers: []
    };
  },
  computed: {
    ...mapState(['userStatus'])
  },
  mounted() {
    this.fetchWeeklyRewards();
    this.fetchTopUsers();
  },
  methods: {
    async fetchWeeklyRewards() {
      try {
        this.isLoading = true;
        const response = await axios.get('/rewards/weekly');
        if (response.data.success) {
          this.weeklyRewards = response.data.weeks;
        }
      } catch (error) {
        console.error('Error fetching weekly rewards:', error);
      } finally {
        this.isLoading = false;
      }
    },
    async fetchTopUsers() {
      try {
        this.isLoadingTopUsers = true;
        const response = await axios.get('/rewards/top-users');
        if (response.data.success) {
          this.topTwitterUsers = response.data.twitterUsers;
          this.topTelegramUsers = response.data.telegramUsers;
        }
      } catch (error) {
        console.error('Error fetching top users:', error);
      } finally {
        this.isLoadingTopUsers = false;
      }
    },
    formatDateRange(start, end) {
      const startDate = new Date(start);
      const endDate = new Date(end);
      return `${startDate.toLocaleDateString()} - ${endDate.toLocaleDateString()}`;
    }
  }
};
</script>

